.custom-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    transform: translateX(100%);
    z-index: 99999999; }
    .custom-modal.show {
      transform: translateX(0%); }
      .custom-modal.show .custom-modal-dialog {
        transform: translateX(0%); }
    .custom-modal .custom-modal-dialog {
      position: absolute;
      transition: transform 0.3s ease-out;
      top: 0px;
      right: 0px;
      width: 100vw;
      height: 100vh;
      background-color: #f2f2ef;
      opacity: 1;
      overflow: auto;
      transform: translateX(100%); }
      @media (min-width: 768px) {
        .custom-modal .custom-modal-dialog {
          width: 550px; } }
      .custom-modal .custom-modal-dialog button.close-btn {
        position: absolute;
        left: 20px;
        top: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background-color: transparent;
        border: none;
        padding: 0px;
        border: 1px solid #a7a5a5; }
        .custom-modal .custom-modal-dialog button.close-btn svg {
          width: 10px;
          height: 10px; }
  
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #cdd1ce;
    transition: opacity 0.15s linear;
    display: none;
    opacity: 0; }
    .modal-overlay.show {
      display: block;
      opacity: 0.6; }
  